<script>
import * as qiniu from "qiniu-js";
import md5 from "md5";
import Exif from "exif-js";
export default {
  name: "Vertify",
  data() {
    return {
      show: false,
      idcard: "", //
      name: "", //
      qiniu: {},
      fileFront: [],
      fileBack: [],
      front: "", //
      back: "", //
      rep: /([A-Za-z](\d{6})\d)|(\d{6})(\d{4})(\d{2})(\d{2})(\d{3})([0-9]|X|x)/,  //
    };
  },
  mounted() {
    this.getInfo();
  },
  methods: {
    uploaderFile(event, index) {
      // console.log(event)
      let _self = this;
      let file = event.file;

      let putExtra = {
        fname: file.name,
        mimeType: ["image/png", "image/jpeg", "image/gif"],
      };

      let config = {
        headers: { "Content-Type": "multipart/form-data" },
      };
      let self = this;
      let Orientation;
      //
      Exif.getData(file, function () {
        Orientation = Exif.getTag(this, "Orientation");
      });
      var reader = new FileReader();
      let url = "";
      let formData = new FormData();
      //   formData.append("file", file);
      reader.readAsDataURL(file);

      reader.onload = function (e) {
        url = this.result.substring(this.result.indexOf(",") + 1);
        url = "data:image/png;base64," + url;
        let img = new Image();
        img.src = this.result;
        img.onload = function () {
          let data = self.compress(img, Orientation);
          // console.log(data);
          //   console.log(_self.convertBase64UrlToBlob(data));
          formData.append(
            "file",
            _self.convertBase64UrlToBlob(data),
            "image.png"
          );
          _self.$axios.upFile(`${_self.$api.upFile}`, formData).then((res) => {
            if (index == 0) {
              _self.front = res.data;
            }
            if (index == 1) {
              _self.back = res.data;
            }
            // console.log(_self.front);
            // console.log(_self.back);
          });
        };
      };
    },
    rotateImg(img, direction, canvas) {
      //4
      const min_step = 0;
      const max_step = 3;
      if (img == null) return;
      //imgimg
      let height = img.height;
      let width = img.width;
      let step = 2;
      if (step == null) {
        step = min_step;
      }
      if (direction == "right") {
        step++;
        //
        step > max_step && (step = min_step);
      } else {
        step--;
        step < min_step && (step = max_step);
      }
      //
      let degree = (step * 90 * Math.PI) / 180;
      let ctx = canvas.getContext("2d");
      switch (step) {
        case 0:
          canvas.width = width;
          canvas.height = height;
          ctx.drawImage(img, 0, 0);
          break;
        case 1:
          canvas.width = height;
          canvas.height = width;
          ctx.rotate(degree);
          ctx.drawImage(img, 0, -height);
          break;
        case 2:
          canvas.width = width;
          canvas.height = height;
          ctx.rotate(degree);
          ctx.drawImage(img, -width, -height);
          break;
        case 3:
          canvas.width = height;
          canvas.height = width;
          ctx.rotate(degree);
          ctx.drawImage(img, -width, 0);
          break;
      }
    },
    compress(img, Orientation) {
      let canvas = document.createElement("canvas");
      let ctx = canvas.getContext("2d");
      //canvas
      let tCanvas = document.createElement("canvas");
      let tctx = tCanvas.getContext("2d");
      let initSize = img.src.length;
      let width = img.width;
      let height = img.height;
      //400
      let ratio;
      if ((ratio = (width * height) / 4000000) > 1) {
        console.log("400");
        ratio = Math.sqrt(ratio);
        width /= ratio;
        height /= ratio;
      } else {
        ratio = 1;
      }
      canvas.width = width;
      canvas.height = height;
      //        
      ctx.fillStyle = "#fff";
      ctx.fillRect(0, 0, canvas.width, canvas.height);
      //100
      let count;
      if ((count = (width * height) / 1000000) > 1) {
        console.log("100W");
        count = ~~(Math.sqrt(count) + 1); //
        //            
        let nw = ~~(width / count);
        let nh = ~~(height / count);
        tCanvas.width = nw;
        tCanvas.height = nh;
        for (let i = 0; i < count; i++) {
          for (let j = 0; j < count; j++) {
            tctx.drawImage(
              img,
              i * nw * ratio,
              j * nh * ratio,
              nw * ratio,
              nh * ratio,
              0,
              0,
              nw,
              nh
            );
            ctx.drawImage(tCanvas, i * nw, j * nh, nw, nh);
          }
        }
      } else {
        ctx.drawImage(img, 0, 0, width, height);
      }
      //ios 
      if (Orientation != "" && Orientation != 1) {
        switch (Orientation) {
          case 6: //90
            this.rotateImg(img, "left", canvas);
            break;
          case 8: //90
            this.rotateImg(img, "right", canvas);
            break;
          case 3: //180
            this.rotateImg(img, "right", canvas); //
            this.rotateImg(img, "right", canvas);
            break;
        }
      }
      //
      let ndata = canvas.toDataURL("image/jpeg", 0.5);
      // console.log("" + initSize);
      // console.log("" + ndata.length);
      // console.log(
      //   "" + ~~((100 * (initSize - ndata.length)) / initSize) + "%"
      // );
      tCanvas.width = tCanvas.height = canvas.width = canvas.height = 0;
      return ndata;
    },
    // base64blob
    convertBase64UrlToBlob: function (urlData) {
      var arr = urlData.split(","),
        mime = arr[0].match(/:(.*?);/)[1],
        bstr = atob(arr[1]),
        n = bstr.length,
        u8arr = new Uint8Array(n);
      while (n--) {
        u8arr[n] = bstr.charCodeAt(n);
      }
      return new Blob([u8arr], {
        type: mime,
      });
    },
    // 
    beforeRead(file) {
      if (
        file.type == "image/jpeg" ||
        file.type == "image/png" ||
        file.type == "image/jpg"
      ) {
        return true;
      }
      this.$toast("jpgjpegjpg");
      return false;
    },
    afterFrontRead(file) {
      this.uploaderFile(file, 0);
    },
    afterBackRead(file) {
      this.uploaderFile(file, 1);
    },
    //
    obtain() {},
    buttonClick() {
      //  else if (!this.rep.test(this.idcard)) {
      //   this.$toast("");
      // } 
      if (!this.name) {
        this.$toast("");
      } else if (!this.idcard) {
        this.$toast("");
      }else if (!this.front) {
        this.$toast("");
      } else if (!this.back) {
        this.$toast("");
      } else {
        // console.log(this.front);
        // console.log(this.back);
        this.$toast.loading("...");
        this.$axios
          .post("realname/save", {
            name: this.name,
            idCard: this.idcard,
            idCardPositive: this.front, //
            idCardNegative: this.back, //
          })
          .then((res) => {
            console.log(res);
            if (res.msg != "success") {
              this.$toast.fail(res.msg);
            } else {
              this.name = null;
              this.idcard = null;
              this.front = null;
              this.back = null;
              this.fileFront = [];
              this.fileBack = [];
              this.$toast.success("");
              this.$router.replace("/authentication/underReview"); //
            }
          });
      }
    },
    getInfo() {
      var that = this;
      this.$axios.post(this.$api.realnameInfo, {}).then((res) => {
        if (res.code != 200) {
          this.$toast(res.msg);
        } else {
          this.$toast.loading("loding...");
          // console.log(that.$route.query.status);
          if (res.data.status == 0 || this.$route.query.status == 0) {
            // this.$toast("");
          } else if (res.data.status == 3) {
            this.$toast.success("");
            this.$router.replace({
              path: "/authentication/authenticationSuccess",
              query: { info: JSON.stringify(res.data) },
            }); //
          } else if (res.data.status == 2) {
            this.$toast("");
            this.$router.replace("/authentication/underReview"); //
          } else {
            this.$toast.fail("");
            this.$router.replace("/authentication/authenticationFail"); //
          }
        }
      });
    },
  },
};
</script>

